.modal {
  background-color: $main-text-color;
  border-radius: 5px;
  padding: 48px 20px 41px;
  width: 280px;
  height: 90vh;
  position: relative;
  @include font(12px, 500, 16px);
  color: $secondary-text-color;

  @include max-width(tablet) {
    overflow-y: auto;
  }

  @include min-width(tablet) {
    width: 618px;
    height: 562px;
    padding: 42px 30px 40px 28px;
  }

  @include min-width(desktop) {
    width: 806px;
    height: 568px;
    padding: 50px 30px 40px 30px;
  }

  &__wraper {
    position: relative;
    display: grid;
    grid-template-columns: repeat(1, 240px);

    @include min-width(tablet) {
      grid-template-columns: repeat(2, 264px);
      grid-gap: 32px;
    }

    @include min-width(desktop) {
      grid-template-columns: 318px 396px;
      grid-gap: 30px;
    }
  }

  &__image-wraper {
    height: 356px;
    width: 240px;
    border-radius: 5px;
    margin-bottom: 30px;
    overflow: hidden;

    @include min-width(tablet) {
      width: 264px;
      height: 373px;
    }
    @include min-width(desktop) {
      width: 318px;
      height: 477px;
    }
  }

  &__image {
    @include min-width(desktop) {
      height: 100%;
      width: auto;
    }
  }

  &__info {
    @include font(12px, 500, 16px);
    margin-bottom: 21px;
  }

  &__info-title {
    @include font(20px, 500, 23px);
    margin-bottom: 20px;
  }

  &__info-wraper {
    display: grid;
    grid-gap: 10px;
    margin-bottom: 20px;
  }

  &__info-item-wraper {
    display: grid;
    grid-template-columns: 108px 1fr;
  }

  &__info-item:not(:last-child) {
    color: $modal-secondary-color;
  }

  &__info-value {
    &--descrip {
      @include font(12px, 500, 20px);
    }

    &--hight {
      @include font(12px, 500, 14px);
    }

    &--border,
    &--transparent {
      text-align: center;
      display: inline-block;
      width: 36px;
      height: 16px;
      padding: 1px;
      border-radius: 5px;
      background-color: #92929229;
    }

    &--border {
      background-color: $accent-color;
      color: $main-text-color;
    }
  }

  &__info-slash {
    color: $modal-secondary-color;
  }

  &__info-about {
    display: grid;
    grid-gap: 10px;
  }

  &__info-about-wraper {
    margin-bottom: 20px;
    max-height: 216px;
    overflow-y: auto;
  }

  &__btn-wraper {
    display: flex;
  }
}

.modal__btn-watched {
  margin-right: 15px;
}
.modal__btn-queue,
.modal__btn-watched {
  color: $secondary-text-color;
  border: 1px solid $secondary-text-color;
  width: 112.53px;
  padding: 5px;
  transition: background, $main-duration $main-timing-function;

  @include min-width(tablet) {
    width: 125px;
  }
  @include min-width(desktop) {
    width: 136px;
  }

  &--active {
    color: $main-text-color;
    border-color: $accent-color;
    border: 1px solid $accent-color;
    background-color: $accent-color;
  }

  &:hover,
  &:focus {
    color: $secondary-text-color;
    border: 1px solid $accent-color;
    background-color: $main-text-color;
    filter: drop-shadow(0px 4px 10px rgba(255, 151, 1, 1.6));
  }
}
